import styled from 'styled-components';

export default (component) => styled(component)`
  .explorer__item {
    display: inline-block;
    width: 20%;
    margin-left: 15px;
    height: 72px;
  }
  .item__photo {
    width: 30%;
    height: 100%;
    object-fit: cover;
  }
  .item__title {
    vertical-align: center;
  }
`;
